    margin-right: 3%;
    border-bottom: 1px solid #d9d9d9;
  }
    .autocomplete-result:hover {
      background-color: #f2f2fc; }
  
  .autocomplete-results {
    position: inherit;
    background-color: #fff;
    z-index: 100;
    border-radius: 0px 0px 10px 10px;
    width: 80%;
    margin-left: 10%;
    border: 1px solid #d9d9d9;
    word-break: keep-all;
    font-size: 16px;
   }

  
